import React, { Component } from 'react';
import Paper from 'material-ui/Paper';
import './App.css';
import SvgCanvas from './SvgCanvas';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import AVPlay from 'material-ui/svg-icons/av/play-arrow';
import AVPause from 'material-ui/svg-icons/av/pause';
import SongDescription from './SongDescription';

export default class SongDetail extends Component {
    constructor (props) {
        super(props);
        this.state = {
            isPlaying: false,
        }
    }
  render() {
    const titleStyle = {
        backgroundColor: "#8F2D56"
    }
    const playBtnStyle = {
        position: "absolute",
        top: 80,
        right: 20,
        zIndex: 5
    }
    return (
        <div id="container">
            <FloatingActionButton style={playBtnStyle} onClick={() => this.togglePlay()}>
                {this.state.isPlaying ? <AVPause /> :<AVPlay />}
            </FloatingActionButton>
            <Paper id="songTitle" style={titleStyle}>
                <h2>
                    ALMIFONIS/ROSA
                </h2>
            </Paper>
            <SvgCanvas />
            <SongDescription />
        </div>
    )
  }
  togglePlay() {
        let currentState = this.state.isPlaying;
        this.setState({isPlaying: !currentState});
  }
}




