import React, { Component } from 'react';
import FlatButton from 'material-ui/FlatButton';


export default class Buttons extends Component {
    render() {
        const containerStyle = {
            display: "flex",
            flexDirection: "column",
            justifyContent: "flex-end"
        }
        const buttonStyle = {
            border: "1px solid white",
            borderRadius: 5,
            marginBottom: 10
        }
        const buttonLabelStyle = {
            color: "white"
        }
        return (
            <div style={containerStyle}>
                <FlatButton style={buttonStyle} label="MEI" labelStyle={buttonLabelStyle}/>
                <FlatButton style={buttonStyle} label="SVG" labelStyle={buttonLabelStyle}/>
                <FlatButton style={buttonStyle} label="MIDI" labelStyle={buttonLabelStyle}/>
            </div>
        );
    }
}




