import React, { Component } from 'react';
import Paper from 'material-ui/Paper';
import './App.css';
import Buttons from './Buttons';
import RaisedButton from 'material-ui/RaisedButton';
import FlatButton from 'material-ui/FlatButton';
import Dialog from 'material-ui/Dialog';
import ImageIframe from './ImageIframe';

export default class SongDescription extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: {
                detail: false,
                image: false
            }
        }
    }
  render() {
        const descriptionStyle = {
            display: "grid",
            gridTemplateColumns: "2fr 1fr 1fr",
            gridTemplateRows: "250px 200px 50px",            
        }
        const titleStyle = {
            gridColumn: "1/2",
            gridRow: "1/2",
            padding: "10px",
            backgroundColor: "#D81159",
            color: "white",
        }
        const infoStyle = {
            gridColumn: "2/3",
            gridRow: "1/2",
            padding: "10px",
            backgroundColor: "#002349",
            color: "white",
        }
        const downloadStyle = {
            gridColumn: "3/4",
            gridRow: "1/2",
            padding: "10px",
            backgroundColor: "#FBB13C",
            color: "white",
        }
        const footerContainerStyle = {
            gridColumn: "1/4",
            gridRow: "2/3",
            backgroundColor: "#218380",
            color: "white",
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
            padding: 10
        }
        const footerStyle = {
            fontWeight: "lighter",
            fontSize: 15,
        }
        const buttonsStyle = {
            display: "flex",
            flexDirection: "row",
        }
        const closeBtn = <FlatButton
            label="Close"
            primary={true}
            keyboardFocused={true}
            onClick={() => this.handleCloseImage()}
        />
    return (
        <Paper id="songDescription" style={descriptionStyle}>
            <Dialog
                title="Manuscript Image from Source"
                actions={closeBtn}
                modal={false}
                open={this.state.isOpen.image}
                onRequestClose={() => this.handleCloseImage()}
            >
                <ImageIframe />
            </Dialog>
            <div style={titleStyle}>
                <h3>ALMIFONIS/ROSA</h3>
                <h4 style={titleStyle}>anonymous</h4>
                <div style={buttonsStyle}>
                    <RaisedButton style={{marginRight: 5}} label="More Detail" primary={true}/>
                    <RaisedButton label="Manuscript Image" onClick={() => this.handleOpenImage()} primary={true}/>
                </div>
            </div>
            <div style={infoStyle}>
                <div className="infoItems">
                    <h3>Info</h3>
                    <div><h5>Concordant Sources</h5></div>
                    <div>Cortona 1, Trem</div>
                    <div><h5>Previous Edition</h5></div>
                    <div>Harrison</div>
                </div>
            </div>
            <div style={downloadStyle}>
                <h3>Downloads</h3>
                <Buttons />
            </div>
            <div style={footerContainerStyle}>
                <p style={footerStyle}>Project Author: Karen Desmond</p>
                <p style={footerStyle}>Website Designer: Kevin Wang</p>
            </div>
        </Paper>
    )
  }
  handleOpenImage() {
      this.setState({isOpen: {image: true}})
  }
  handleCloseImage() {
    this.setState({isOpen: {image: false}})
}
}




